/* OpenMoji CSS */
img.emoji {
    display: inline;

    /* stolen from Discord lol */
    width: 1.375em;
    height: 1.375em;
    object-fit: contain;
    vertical-align: bottom;
}

.openmoji {
    font-family: OpenMojiColor, ui-sans-serif, system-ui, sans-serif;
}

/* OpenMoji end */

/* util */
.mono-font {
    font-family: ui-monospace, monospace;
}

/* util end */

/* base */
:root {
    min-height: 100%;
    accent-color: var(--main);
    background: var(--bg);
    color: var(--text-color);
    font-family: ui-sans-serif, system-ui, sans-serif;
    overflow-x: hidden;

    /* other useful stuff: */
    --header-height: 4rem;

    /* TOOLTIP STUFF */

    /* offset moves tooltip to the center of the parent */
    --tp-offset: -0.15rem;

    /* make stuff easier for people that steal this CSS file */
    --tp-text-color: var(--text-color);
    --tp-border-color: var(--main-2);
    --tp-bg-color: var(--bg);
}

#skip-to-content {
    position: absolute;
    z-index: 10;
    top: 0;
    left: 1rem;
    height: 2rem;
    padding: 0 1rem;
    border-radius: 0 0 0.5rem 0.5rem;
    background-color: var(--main);
    color: var(--text-color);
    font-weight: bold;
    transform: translateY(-3rem);
}

#skip-to-content:focus {
    top: var(--header-height);
    transform: translateY(0);
}

body {
    margin: 0;
}

main {
    display: flex;
    width: 96vw;
    max-width: calc(100% - 4vw);
    flex-direction: column;
    align-items: center;
    justify-content: center;

    /* top, right, bottom, left */
    padding: 0.5rem 2vw 0;
}

/* .text is for version page */
main,
.text,
a {
    hyphens: auto;
    line-break: auto;
    white-space: normal;
}

.center-text {
    text-align: center;
}

a:not(.no-style-a, .no-style-a-block, .no-style-a-emoji) {
    color: var(--main);
}

.no-style-a,
.no-style-a-block,
.no-style-a-emoji {
    color: inherit;
    text-decoration: none;
}

.no-style-a-block {
    display: block;
}

.no-style-a:hover,
.no-style-a-block:hover {
    text-decoration: underline;
}

.hidden {
    position: absolute;
    display: none;
    width: 0 !important;
    max-width: 0 !important;
    height: 0 !important;
    opacity: 0;
    pointer-events: none;
    visibility: collapse;
}

/* base end */

/* footer */
footer {
    width: 100dvw;
    max-width: 100%;
    margin-top: 2.6rem;
    margin-bottom: 0.5rem;
    text-align: center;
}

#ai-policy {
    font-size: 0.5rem;
    text-align: center;
    padding: 0.7rem;
}

#openmoji-attribution {
    margin-top: 1.2rem;
    font-size: 0.69rem;
}

/* footer end */

/* header */
header {
    position: sticky;
    z-index: 2;
    top: 0;
    overflow: hidden;
    width: 100vw;
    max-width: 100%;
    height: var(--header-height);
    background-color: var(--main);
    color: #fff;
}

#title,
#back-to-home {
    position: absolute;
    top: 50%;
    margin: 0;
    transform: translateY(-50%);
}

#title {
    left: calc(1rem + calc(var(--header-height) / 1.55));
    font-size: 2rem;
    white-space: nowrap;
}

#back-to-home {
    left: 0.5rem;
    width: calc(var(--header-height) / 1.55);
    height: calc(var(--header-height) / 1.55);
    border-width: 0;
    border-style: solid;
    border-radius: 50%;
}

#back-to-home img {
    width: 100%;
    height: 100%;
}

@media (width <=31rem) {
    #title {
        visibility: hidden;
    }

    #title::before {
        content: attr(short_title);
        visibility: visible;
    }
}

#open-pane {
    position: absolute;
    top: calc(calc(var(--header-height) - calc(var(--header-height) / 2)) / 2);
    right: calc(calc(var(--header-height) - calc(var(--header-height) / 2)) / 2);
    width: calc(var(--header-height) / 2);
    height: calc(var(--header-height) / 2);
}

#open-pane:hover,
#open-pane:focus {
    transform: rotateY(30deg);
}

.site-pane-a {
    color: var(--main-2) !important;
}

#site-pane {
    position: fixed;
    z-index: 11;
    top: 0;
    right: -70%;
    max-width: 70%;
    height: 100vh; /* fallback for browsers that do not support dvh */
    height: 100dvh; /* See: https://caniuse.com/viewport-unit-variants */
    background-color: var(--secondary-bg);
    overflow-y: auto;
    transition: right 0.5s;
}

#site-pane[state="open"],
#site-pane:hover,
#site-pane:focus-within,
#open-pane:focus + #site-pane,
#open-pane:hover + #site-pane {
    right: 0;
}

#site-pane h2 {
    margin: 0.625rem;
}

#site-pane ul {
    padding-right: 0.25em;
}

.list {
    padding-left: 1em;
    list-style-type: none;
}

.sub-list {
    padding-left: 2rem;
    list-style-type: none;
}

.list li,
.sub-list li {
    display: block;
}

.list li::before,
.sub-list li::before {
    display: inline-block;
    margin-right: 0.2rem;
    margin-bottom: 0.1em;
    content: "\2022";
}

.sub-list li::before {
    position: relative;
    content: "\25AA";
    font-size: 0.6rem;
}

.contact-anchor {
    display: block;
    padding: 0.625rem;
    margin: 1.25rem;
    background-color: var(--contrast);
    color: var(--text-color) !important;
    text-align: center;
}

.contact-anchor:hover {
    transform: scale(1.05, 1.05);
}

#oekostrom {
    display: flex;
    width: 100%;
    justify-content: center;
    padding: 0.2rem 0;
}

#oekostrom img {
    width: 8rem;
    max-width: 85%;
    height: auto;
}

/* header end */
